import React from "react";

const Education = ({ college, course, duration, result }) => {
    return (
        <div data-aos="fade-up" className=" col-12 col-lg-6 project-card">
            <h2>{college}</h2>
            <h3>{course}</h3>
            <h3>{duration}</h3>
            <h3>{result}</h3>
        </div>
    )
}

export default Education